	module.exports = {"data":{"site":{"siteMetadata":{"title":"Gatsby Starter Blog","author":"Kyle Mathews"}},"markdownRemark":{"id":"/Users/chris/playground/gatsby-blog/src/pages/my-second-post/index.md absPath of file >>> MarkdownRemark","html":"<p>Wow! I love blogging so much already.</p>\n<p>Did you know that “despite its name, salted duck eggs can also be made from\nchicken eggs, though the taste and texture will be somewhat different, and the\negg yolk will be less rich.”?\n(<a href=\"http://en.wikipedia.org/wiki/Salted_duck_egg\">Wikipedia Link</a>)</p>\n<p>Yeah, I didn’t either.</p>\n<p>Here’s some jsx:</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-none\"><code><div>\n  <Helmet title={siteTitle} />\n  <Bio />\n  {posts.map(({ node }) => {\n    const title = get(node, 'frontmatter.title') || node.fields.slug\n    return (\n      <div key={node.fields.slug}>\n        <h3\n          style={{\n            marginBottom: rhythm(1 / 4),\n          }}\n        >\n          <Link style={{ boxShadow: 'none' }} to={node.fields.slug}>\n            {title}\n          </Link>\n        </h3>\n        <small>{node.frontmatter.date}</small>\n        <p dangerouslySetInnerHTML={{ __html: node.excerpt }} />\n      </div>\n    )\n  })}\n</div></code></pre>\n      </div>","frontmatter":{"title":"My Second Post!","date":"May 06, 2015"}}},"pathContext":{"slug":"/my-second-post/"}}

